@mixin onTablet {
  @media (min-width: 767px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1199px) {
    @content;
  }
}

@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}
